*,
*:before,
*:after {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.bg-color {
  background-color: #fff;
  /**	background: linear-gradient(to bottom right, #007fc2, #00d6ee);**/
}

.visually-hidden {
  width: 1px;
  height: 1px;
  margin-top: -1px;
  margin-left: -1px;
  overflow: hidden;
  position: absolute;
  clip: rect(0, 0, 0, 0);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.header {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  background: #fff;
  color: #000;
  font-size: 48px;
  padding-top: 100px;
  padding-bottom: 50px;
}

.rows {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.card {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(33.333333333333336% - 20px);
  padding: 50px 30px 60px 30px;
  background: #fff;
  border-radius: 10px;
  margin-bottom: 20px;
  list-style-type: none;
  box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, 0.17);
}

.card__photo {
  border-radius: 50%;
}

.card__title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  background: #fff;
  color: #000;
  font-size: 18px;
  line-height: 50px;
  padding-bottom: 10px;
  padding-top: 20px;
}

.card__feedback {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  background: #fff;
  color: #000;
  font-size: 16px;
  line-height: 24px;
  padding-bottom: 25px;
}

.card__date {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  background: #fff;
  font-size: 16px;
  line-height: 24px;
  margin-top: 10px;
  color: #777777;
  font-style: italic;
}

.feature {
  background-color: #fff;
  margin-bottom: 45px;
}

.feature__head {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  background: #fff;
  color: #000;
  font-size: 48px;
  margin-top: 30px;
  margin-bottom: 25px;
}

.feature__about {
  margin: 0 auto;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  background: #fff;
  color: #000;
  font-size: 16px;
  line-height: 21px;
  width: 566px;
}

.feature__icon {
  border-radius: 50%;
  margin-bottom: 20px;
  background-color: #eaf4eb;
  height: 100px;
  width: 100px;
  padding: 15px;
  position: relative;
  left: 100px;
}

.feature__text {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  background: #fff;
  color: #000;
  font-size: 16px;
  margin: 0 auto;
}

.feature__rows {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.feature__rows .feature__card {
  margin-top: 30px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(25% - 20px);
  margin-bottom: 25px;
  list-style-type: none;
}

@media screen and (max-width: 1100px) {
  .rows {
    justify-content: flex-start;
  }

  .card {
    width: calc(33.333333333333336% - 20px);
  }

  .feature__rows {
    justify-content: center;
  }

  .feature__rows .feature__card {
    width: calc(25% - 20px);
  }
}

@media screen and (max-width: 960px) {
  .card {
    width: calc(50% - 20px);
  }

  .feature__rows .feature__card {
    width: calc(33.333333333333336% - 20px);
  }
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .card {
    width: calc(50% - 20px);
  }

  .feature__rows .feature__card {
    width: calc(50% - 20px);
  }
}

@media screen and (max-width: 560px) {
  .rows {
    justify-content: flex-start;
  }

  .card {
    width: calc(100% - 20px);
  }

  .feature__rows {
    justify-content: center;
  }

  .feature__rows .feature__card {
    width: calc(100% - 20px);
  }
}